          </div>
          <div class="line-item-box clear">
            <div class="line-item">
              <div class="line-item-title">
                <span>有效证件类型</span>
              </div>
              <div class="line-item-input">
                <el-input placeholder="请填写" class="header-right-time right" :clearable='true' v-model="infoDataObj.document_type"></el-input>
              </div>
            </div>
            <div class="line-item">
              <div class="line-item-title">
                <span>有效证件号码</span>
              </div>
              <div class="line-item-input">
                <el-input placeholder="请填写" class="header-right-time right" :clearable='true' v-model="infoDataObj.document_no"></el-input>
              </div>
