              </div>
            </div>
          </div>
          <div>
            <div class="step1-select-box select-normal select-line">
              <span>是否开启客户端账号</span>
              <el-radio-group v-model="infoDataObj.status">
                <el-radio :label="item.id" v-for="(item,index) of radio" :key="index">{{item.name}}</el-radio>
              </el-radio-group>
            </div>
            <div class="step1-select-box select-normal" v-if="infoDataObj.status=='1'">
              <span>登录手机号</span>
              <el-input placeholder="请填写" class="header-right-time right" :clearable='true' v-model="infoDataObj.phone" maxlength='11'></el-input>
              <span class="inputbox-error" v-if="phone">{{phoneErrormsg}}</span>
            </div>
            <div class="step1-select-ts">提示：勾选后将会以手机接收短信的方式进行登录</div>
          </div>
        </div>
        <div class="clear step2-box">
          <div class="step-title step2-title">Part 2：基金购买信息</div>
          <!-- 数据卡片 -->
          <div class="account-itembox" v-for="(item,index) of infoDataArr" :key="index" @click="changeFund(item,index)">
            <div class="account-item">
              <div class="account-item-state">
                <img src="../../images/investorInfo_state_cb.png" alt="" v-if="item.type==1">
                <img src="../../images/investorInfo_state_cx.png" alt="" v-if="item.type==2">
                <img src="../../images/investorInfo_state_tc.png" alt="" v-if="item.type==3">
              </div>
              <div class="account-item-name">
                {{item.fundName}}
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon  icon-gufen"></span>
                  <p class="text">持有份额(份)：{{item.portion}}</p>
                </div>
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon  icon-money"></span>
                  <p class="text">投资额(万元)：{{item.inputMoney}}</p>
                </div>
              </div>
              <div class="account-item-infobox clear">
                <div class="account-item-infobox-item">
                  <span class="account-icon  icon-time"></span>
                  <p class="text">购买时间：{{item.buy_time}}</p>
                </div>
              </div>
              <div class="account-item-btnbox clear">
                <div class="account-item-btn account-item-btn-delete" @click.stop="deleteFund(item.id,index)">
                  <span class="account-icon icon-delete"></span> 删除基金
                </div>
              </div>
            </div>
          </div>
          <!-- 添加账号卡片-->
          <div class="account-itembox">
            <div class="account-item account-additem">
              <span class="account-icon icon-addaccount" @click="createDialogShow=true;isEditor=false"></span> 添加持有基金
            </div>
          </div>
        </div>
        <div class="step3-box">
          <button class="btn-blue" v-if="isCreatInfo" @click="addInvestor">提交</button>
          <button class="btn-blue" v-else @click="editInvestor">提交</button>
        </div>
      </div>
      <!-- 弹窗 -->
      <el-dialog title="购买的基金" :visible.sync="createDialogShow" width="570px" center :append-to-body='true' :before-close="toCancleConfirm">
        <div class="dialog_content">
          <div class="form-group margin-bottom-none">
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>基金</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select placeholder="请选择" v-model="alertDataObj.fund_id" filterable @change="dataChange">
                    <el-option v-for="item of fundList" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error" v-if="fund">{{fundErrormsg}}</span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>持有份额(份)</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <input class="form-group-inputbox-inputtext" type="text" v-model="alertDataObj.portion" @change="dataChange">
                </div>
                <span class="form-group-inputbox-error" v-if="portion">{{portionErrormsg}}</span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear ">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title">投资额(万元)</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <input class="form-group-inputbox-inputtext" type="text" v-model="alertDataObj.inputMoney" @change="dataChange">
                </div>
                <span class="form-group-inputbox-error" v-if="inputMoney">{{inputMoneyErrormsg}}</span>
              </div>
            </div>
          </div>
          <div class="form-group-inputboxgroup clear margin-bottom-none">
            <div class="form-group-inputbox">
              <h2 class="form-group-inputbox-title">购买时间</h2>
              <div class="form-group-inputbox-selectbox select-normal-40 left">
                <el-date-picker type="date" placeholder="选择日期" v-model="alertDataObj.buy_time" :editable='false' :clearable='true' value-format="yyyy-MM-dd" class="header-right-time right" @change="dataChange">
                </el-date-picker>
              </div>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" @click="alertConfirmEvent">确定</button>
          <button class="dialog-btn-submit btn-blue-border" @click="toCancleConfirm">取消</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  allProjectOrFund,
  addCusInfo,
  getCusInfo,
  delCusFund,
  editCusInfo
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      createDialogShow: false, //新增账户弹窗显示
      infoDataObj: {
        status: 2,
        name: '',
        cell: '',
        email: '',
        phone: '',
        risk: '',
      }, //存放基本信息的内容
      infoDataArr: [], //存放基金信息数组
      alertDataObj: {}, //存放弹窗里面基金信息
      alertIndex: null, //修改时展示的信息在表格数组的下标
      fundList: [], //存放基金列表
      radio: [{
          id: 1,
          name: '是'
        },
        {
          id: 2,
          name: '否'
        }
      ], //单选框的选项
      isCreatInfo: true,
      investorId: null,
      nameErrormsg: '',
      cellErrormsg: '',
      emailErrormsg: '',
      phoneErrormsg: '',
      fundErrormsg: '',
      inputMoneyErrormsg: '',
      portionErrormsg: '',

      notConfim: true,
      notAlertConfim: true,
      isEditor: false,
      canSave: false,
      page: 1,//上个页面传过来的page页面
    }
  },
  created() {
    this.getFundList();
    if (this.$route.query.id != undefined) {
      this.isCreatInfo = false;
      this.investorId = this.$route.query.id;
      //获取
      this.getInvestorInfo(this.investorId)
    }
    if (this.$route.query.page) {
    		this.page = this.$route.query.page;
    }
  },
  mounted() {

  },
  computed: {
    name: function() {
      if (this.notConfim) {
        return false
      }
      if (this.infoDataObj.name == '' || this.infoDataObj.name == undefined) {
        this.nameErrormsg = "*投资人姓名不能为空";
        return true;
      } else {
        return false;
      }
    },
    cell: function() {
      if (this.notConfim) {
        return false
      }
      let phoneTest = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (this.infoDataObj.cell == '' || this.infoDataObj.cell == undefined) {
        this.cellErrormsg = "*手机号码不能为空"
        return true;
      } else if (!phoneTest.test(this.infoDataObj.cell)) {
        this.cellErrormsg = "*手机号码格式不正确"
        return true;
      } else {
        return false;
      }
    },
    email: function() {
      if (this.notConfim) {
        return false
      }
      let emailTest = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (this.infoDataObj.email == '' || this.infoDataObj.email == undefined) {
        return false;
      } else {
        if (!emailTest.test(this.infoDataObj.email)) {
          this.emailErrormsg = "*邮箱格式不正确"
          return true;
        } else {
          return false;
        }
      }
    },
    phone: function() {
      if (this.notConfim) {
        return false
      }
      let phoneTest = /^[1][3,4,5,7,8][0-9]{9}$/;
      if (this.infoDataObj.status == 1) {
        if (this.infoDataObj.phone == '') {
          this.phoneErrormsg = "*登陆手机号码不能为空"
          return true;
        } else if (!phoneTest.test(this.infoDataObj.phone)) {
          this.phoneErrormsg = "*登陆手机号码格式不正确";
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    },
    fund: function() {
      if (this.notAlertConfim) {
        return false
      }
      if (this.alertDataObj.fund_id == '' || this.alertDataObj.fund_id == undefined) {
        this.fundErrormsg = "*请选择基金";
        return true;
      } else {
        return false;
      }
    },
    portion: function() {
      if (this.notAlertConfim) {
        return false
      }
      let numberTest = /^[0-9]+([.]{1}[0-9]+){0,1}$/
      if (this.alertDataObj.portion == '' || this.alertDataObj.portion == undefined) {
        this.portionErrormsg = "*持有份额不能为空"
        return true;
      } else if (!numberTest.test(this.alertDataObj.portion)) {
        this.portionErrormsg = "*只能输入数字"
        return true;
      } else {
        return false;
      }
    },
    inputMoney: function() {
      if (this.notAlertConfim) {
        return false
      }
      if (this.alertDataObj.inputMoney == '' || this.alertDataObj.inputMoney == undefined) {
        return false;
      } else {
        if (!/^[0-9]+([.]{1}[0-9]+){0,1}$/.test(this.alertDataObj.inputMoney)) {
          this.inputMoneyErrormsg = "*只能输入数字"
          return true;
        } else {
          return false;
        }
      }
    },

  },
  methods: {
    //添加投资人信息
    addInvestor() {
      // debugger;
      this.notConfim = false;
      if (!this.name && !this.cell && !this.email && !this.phone) {
        let params = {
          info: this.infoDataObj,
        }
        if (this.infoDataArr.length == 0) {
          dialogConfirm('info', "请先添加持有基金");
          return false;
        } else {
          if (this.infoDataObj.status == 2) {
            this.infoDataObj.phone = ''
          }
          let newInfoDataArr = [];
          let number = 0;
          for (let item of this.infoDataArr) {
            newInfoDataArr[number] = {};
            if (item.id != undefined) {
              newInfoDataArr[number].id = item.id;
            }
            newInfoDataArr[number].fund_id = item.fund_id
            newInfoDataArr[number].portion = item.portion
            newInfoDataArr[number].inputMoney = item.inputMoney
            newInfoDataArr[number].buy_time = item.buy_time
            number++;
          }
          params.data = newInfoDataArr;
        }
        addCusInfo(params).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '添加成功');
            this.$router.push({
              path: '/home/investorsInfoList',
            })
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      }
    },
    //修改投资人信息
    editInvestor() {
      this.notConfim = false;
      if (!this.name && !this.cell && !this.email && !this.phone) {
        if (this.infoDataArr.length == 0) {
          dialogConfirm('info', "请先添加持有基金");
          return false;
        }
        this.infoDataObj.id = this.investorId;
        if (this.infoDataObj.status == 2) {
          this.infoDataObj.phone = ''
        }
        let newInfoDataArr = [];
        let number = 0;
        for (let item of this.infoDataArr) {
          newInfoDataArr[number] = {};
          if (item.id != undefined) {
            newInfoDataArr[number].id = item.id;
          }
          newInfoDataArr[number].fund_id = item.fund_id
          newInfoDataArr[number].portion = item.portion
          newInfoDataArr[number].inputMoney = item.inputMoney
          newInfoDataArr[number].buy_time = item.buy_time
          number++;
        }
        editCusInfo({
          info: this.infoDataObj,
          data: newInfoDataArr
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '修改成功');
            this.$router.push({
              path: '/home/investorsInfoList',
            })
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      }


    },
    //获取投资人信息详情
    getInvestorInfo(id) {
      getCusInfo({
        id: id
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          this.infoDataObj = data.info;
          this.infoDataArr = data.data;
        }
      })
    },
    //获取基金下拉列表的数据
    getFundList() {
      allProjectOrFund({
        type: 2,
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.fundList = res.data.payload.data;
        }
      })
    },

    //修改基金内容
    changeFund(item, index) {
      this.alertIndex = index;
      this.isEditor = true;
      this.alertDataObj = JSON.parse(JSON.stringify(item));
      this.createDialogShow = true;
    },

    //删除基金
    deleteFund(id, index) {
      if (id != undefined) {
        dialogConfirmHasbtn('确定删除该基金？').then(() => {
          delCusFund({
            id: id,
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '删除成功')
              this.infoDataArr.splice(index, 1);
            }
          })
        }).catch(() => {});
      } else {
        this.infoDataArr.splice(index, 1);
      }
    },
    //弹出框确定按钮
    alertConfirmEvent() {
      this.notAlertConfim = false; //才开始进行自动验证
      if (!this.portion && !this.fund && !this.inputMoney) {
        if (this.infoDataArr.length > 0) {
          let number = 0;
          for (let item1 of this.infoDataArr) {
            if (item1.fund_id == this.alertDataObj.fund_id) {
              if (this.isEditor) {
                if (number != this.alertIndex) {
                  dialogConfirm('info', '基金已经存在');
                  return false;
                }
              } else {
                dialogConfirm('info', '基金已经存在');
                return false;
              }

            }
            number++;

          }
        }

        for (let item of this.fundList) {
          if (item.id == this.alertDataObj.fund_id) {
            this.alertDataObj.fundName = item.name
            this.alertDataObj.type = item.type
          }
        }
        if (this.isEditor) {
          this.infoDataArr[this.alertIndex] = this.alertDataObj;
          this.isEditor = false;
        } else {
          this.infoDataArr.push(this.alertDataObj);
        }
        this.createDialogShow = false;
        this.canSave = false;
        this.notAlertConfim = true;
        this.alertDataObj = {};
      }

    },

    dataChange() {
      this.canSave = true;
    },
    //取消提交
    toCancleConfirm() {
      if (this.canSave) {
        dialogConfirmHasbtn('确定不提交并关闭么？').then(() => {
          this.canSave = false;
          this.createDialogShow = false;
          this.alertDataObj = {};
          this.notAlertConfim = true;
        }).catch(() => {});
      } else {
        this.createDialogShow = false;
        this.alertDataObj = {};
        this.notAlertConfim = true;
      }
    }
  }
};

</script>
<style lang="scss" scoped>
