  font-size: 18px;
  color: #3C98E8;
  margin-bottom: 5px;
  font-weight: 500;
}

.step1-tisi {
  font-size: 14px;
  color: #A0A4AA;
  margin-bottom: 10px;
}

.step1-select-box {
  line-height: 30px;
  height: 30px;
  margin-bottom: 25px;
  display: inline-block;
  width: 325px;
  margin-right: 46px;
  position: relative;
}

.select-line {
  width: 240px;
}

.step1-select-box .inputbox-error {
  position: absolute;
  font-size: 12px;
  color: red;
  left: 85px;
  top: 28px;
}

.step1-select-box span {
  font-size: 14px;
  color: #35383A;
}

.step1-select-ts {
  font-size: 14px;
  position: relative;
  top: -20px;
  letter-spacing: 1px;
  color: #A0A4AA
}

.step2-title {
  margin-bottom: 20px;
  margin-top: 20px;
} //卡片样式
.account-itembox {
  float: left; // width: 25%;
  box-sizing: border-box; // padding-right: 16px;
  // max-width: 500px;
  // min-width: 380px;
  width: 365px;
  margin-right: 10px;
} // .account-itembox:nth-of-type(3n+0) {
//     margin: 0 10px;
// }
.account-itembox:hover {
  cursor: pointer;
}

.account-item {
  position: relative;
  margin-bottom: 16px;
  width: 100%;
  height: 190px;
  overflow: auto;
  padding: 22px 20px 10px;
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #E9E9E9;
  box-shadow: 0px 0px 10px 0px rgba(207, 207, 207, 0.5);
}

.account-item-name {
  // display: inline-block;
  // width: 100%;
  color: #35383A;
  font-size: 20px;
  line-height: 1em;
  font-weight: bold;
  margin-bottom: 17px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.account-item-work {
  display: inline-block;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  color: #A0A4AA;
  background-color: #F3F5F7;
  border-radius: 8px;
  padding: 0px 10px;
  position: relative;
  top: -3px;
}

.account-item-infobox {
  width: 100%;
  margin-bottom: 5px;
  display: flex;
}

.account-item-infobox-item {
  width: 100%;
  display: flex;
  white-space: nowrap; //overflow: hidden;
  margin-bottom: 5px;
}

.account-item-infobox-item:last-child {
  //margin-right: 0px;
}

.account-item-infobox-item .text {
  font-size: 14px;
  color: #A0A4AA;
  white-space: nowrap;
  line-height: 16px;
  margin-left: 6px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.account-item-spanbox {
  min-height: 52px;
  margin-bottom: 5px;
}

.account-item-spanbox-item {
  background: #ECF3FC;
  font-size: 12px;
  color: #4A90E2;
  line-height: 20px;
  padding: 0px 6px;
  margin-bottom: 6px;
  margin-right: 9px;
  float: left;
}

.step3-box {
  width: 100%;
  text-align: left;
  margin-top: 68px;
}

.step3-box button {
  width: 140px;
  height: 40px;
  font-size: 18px;
} //图标
.account-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background: url("../../images/accountmanage_icon.png") no-repeat;
}

.icon-time {
  display: flex;
  flex-shrink: 0;
  background-position: -23px 0px;
}

.icon-money {
  background: url("../../images/icon_money.png") no-repeat center;
}

.icon-gufen {
  background: url("../../images/icon_gufen.png") no-repeat center;
}

.icon-childaccount {
  float: left;
  margin-right: 5px;
  background-position: 0px -78px;
}

.icon-addaccount {
  width: 80px;
  height: 80px;
  display: block;
  margin: 0 auto 14px;
  margin-top: 15px;
  background-position: -144px 0px;
}

.icon-required {
  width: 15px;
  height: 15px;
  background-position: 0px -98px;
}

.account-additem {
  cursor: pointer;
  text-align: center;
  line-height: 1em;
  font-size: 18px;
  color: #35383A;
}

.account-item-btn {
  padding: 3px 5px;
  float: right;
  border: 1px solid #F4F4F4;
  color: #808080;
  background: #fff;
  font-size: 14px;
  margin-left: 16px;
  cursor: pointer;
  border-radius: 2px;
}

.account-item-btn .account-icon {
  width: 16px;
  height: 16px;
  float: left;
  margin: 2px 6px 0px 0px;
}

.account-item-state {
  width: 50px;
  height: 50px;
  position: absolute;
  right: 0px;
  top: 0px;
}

.account-item-state img {
  width: 100%;
  height: 100%;
}

.account-item-btn-delete:hover {
  background: rgba(240, 20, 0, 0.1);
  border-color: rgba(240, 20, 0, 0.1);
  color: #F01400;
}

.account-item-btn-delete:hover .account-icon {
  background-position: -23px -58px;
}

.icon-delete {
  background-position: 0px -58px;
}



/* 弹窗样式 */

.form-group-inputbox {
  width: 470px;
  margin-right: 0px;
}

.form-group-inputboxgroup {
  margin-bottom: 25px;
}

.dialog-btn-submit.btn-blue-border {
  margin-right: 20px;
}

.line {
  width: 100%;
  border-top: 1px solid #F4F4F4;
  margin-top: 25px;
  margin-bottom: 30px;
} //新的内容的样式
.line-item-box {
  .line-item {
    display: inline-block;
    width: 240px;
    margin: 0 50px 0px 0px;
    float: left;
    .line-item-title {
      font-size: 14px;
      color: #35383A;
      padding-bottom: 5px;
    }
    .line-item-input {
      height: 48px;
      .line-item-input-error {
        font-size: 12px;
        color: red;
      }
    }
  }
}

</style>



